import React, { Component } from 'react'
import './index.css';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

export class Search extends Component {
  constructor() {
    super();
    this.state = {filterdropdown:''};
  }
 
  // handleClick(val,e) {
  //   var d = document.getElementById("CategoryBox").value;
  // }
  render() {
    // alert(global.selectedcheckbox);
    
        let searchOnChange = this.props.onChange;
        var strUser;
        global.filterdropdown=this.state.filterdropdown;

        function selectBox(){         
            var e = document.getElementById("selectBox");
            strUser = e.value;
            global.selectedDropdownContract=strUser;                   
            searchOnChange(global.selectedDropdownContract);
          }
    return (
      <div className="ml-auto mr-auto search">
          <Form inline >
            <input type="text" id="selectBox" onKeyPress={(ev) => {
    // console.log(`Pressed keyCode ${ev.key}`);
    if (ev.key === 'Enter') {
      selectBox();
      ev.preventDefault();
    }
  }} placeholder="Search" className="" autoComplete="off" />
          {/* <select id="CategoryBox" className="" onChange={this.handleClick.bind(this,this.value)}>
            <option value="ASSET">ASSET HUB</option>
            <option  value="WIN">WIN STORY</option>
          </select> */}
          <Button variant="primary btn-lg" onClick={selectBox}><img src="../img/Icon_Search.png" /></Button>
          </Form>
      </div>
    )
  }
}

export default Search
